import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser'

import { FoodPage } from './food';
import { MeetingPage } from './meeting';
import { RoomPage } from './room';
import { EditFoods } from './editfood';
import { WeddingDetailPage } from './weddingIframe';

import { ProductService } from '../../providers/product';


@Component({
	selector: 'page-wedding',
	templateUrl: 'wedding.html',
	providers: [ProductService]
})
export class WeddingPage {
	public token = localStorage.getItem('token');
	public account_id = localStorage.getItem('account_id');

	public admin = true;
	public showedit = false;

	public imgSrc = "assets/imgs/product/stylebg.jpg";
	// public imgSrc = "assets/imgs/index/bg.jpg";

	public menulists = [];
	public productlist = [];
	public cartlist = [];
	public brightMenu = [];  //
	public personType = []; //

	public showcart = false;

	public modalVisible = false;
	public modalOrderVisible = false;
	public modalAddVisible = false;
	public modalBaoJiaVisible = false;
	public placeForm = {};

	public second = 2;  //
	public secondname;  //
	public typeP_num = 0;	//

	public chooseTypeValue = '0';

	public taocanlist = [];
	public showTaocanName = false;
	public selectValue = 1;

	public showMoreBtn = false;

	public modalPersonVisible = false;

	public imgUpLoad;  //

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public alertCtrl: AlertController,
		private sanitizer: DomSanitizer,
		public service: ProductService
	) {
		this.getMemu();
		this.getproducts();
	}

	getMemu() {
		this.menulists = [{id:2, name:''}, {id:3, name:''}, {id:4, name:''}, {id:1, name:''}];
		this.secondname = this.menulists[0]['name'];  //
	}

	//
	getproducts() {
		//
		this.getSetList();
	}

	//
	getSetList() {
		this.productlist = [];
		this.service.getSetList(this.token).then(data => {
			if(data.status==1){
				for (let i = 0; i < data['list'].length; i++) {
					let t = {
						id: data['list'][i]['id'],
						img: data['list'][i]['poster_url'],
						title: data['list'][i]['product_name'],
						detail: data['list'][i]['description'],
						price: data['list'][i]['price']
					};
					this.productlist.push(t);
				};
				this.typeP_num = data['list'].length;
			}
		})
	}

	//
	getBrightLight(type){
		this.productlist = [];
		this.service.getBrightPoint(this.token, type).then(data => {
			// if(data.status==1){
				for (let i = 0; i < data['point'].length; i++) {
					let t = {
						id: data['point'][i]['id'],
						img: data['point'][i]['poster_url'],
						title: data['point'][i]['product_name'],
						detail: data['point'][i]['description'],
						price: data['point'][i]['price']
					};
					this.productlist.push(t);
				};
				this.typeP_num = data['point'].length;
				this.brightMenu = data['type_list'];
			// }
		})
	}

	//
	getServicePerson(type){
		this.productlist = [];
		this.service.getServicePerson(this.token, type).then(data => {
			if(data.status==1){
				for (let i = 0; i < data['service_person'].length; i++) {
					let t = {
						id: data['service_person'][i]['id'],
						img: data['service_person'][i]['avatar'],
						title: data['service_person'][i]['name'],
						detail: data['service_person'][i]['description'],
						price: data['service_person'][i]['price']
					};
					this.productlist.push(t);
				};
				this.typeP_num = data['service_person'].length;
				this.personType = data['type'];
				console.log(this.personType);
			}
		})
	}


	editProducts() {
		this.showedit = true;
	}

	editSucess() {
		this.showedit = false;
	}

	// 
	addProducts() {
		this.placeForm = {};
		this.modalVisible = true;
		this.getproducts();
	}

	// 
	editItem(id) {
		this.placeForm = {
			name: '',
			price: '1998',
			detail: ''
		}
		// this.modalVisible = true;
		this.navCtrl.push(WeddingDetailPage, { name: '' });
	}

	deltaocanItem(id) {
		let alert = this.alertCtrl.create({
			title: '',
			// subTitle: 'Your friend, Obi wan Kenobi, just accepted your friend request!',
			buttons: ['']
		});
		alert.present();
	}

	formSubmit(form) {
		console.log(form);
	}

	ifshowName() {
		console.log(this.selectValue);
		if (this.selectValue == 1) { //
			this.showTaocanName = true;
		} else {
			this.showTaocanName = false;
		}
	}
	formAddSubmit(form) {
		console.log(form);
		if (form.from == 1) { //
			this.navCtrl.push(WeddingDetailPage, { name: '' });
		} else if (form.from == 2) { //
			this.navCtrl.push(WeddingDetailPage, { name: '' });
		}
		this.hideAddModal();
	}

	hideModal() {
		this.modalVisible = false;
	}

	// 
	clearCart() {

	}

	//
	addCart(id) {
		console.log(id);
		this.modalOrderVisible = true;
	}
	hideOrderModal() {
		this.modalOrderVisible = false;
	}
	addTaocan() {
		this.modalAddVisible = true;
		this.selectValue = 1;
		this.showTaocanName = false;
		this.taocanlist = [
			{
				id: 1,
				title: ''
			},
			{
				id: 2,
				title: ''
			}
		];
	}
	hideAddModal() {
		this.modalAddVisible = false;
	}

	showCart() {
		this.showcart = true;
		this.getCartlist();
	}

	hideCart() {
		this.showcart = false;
	}

	//
	addBaojia(id) {
		console.log(id);
		this.modalBaoJiaVisible = true;
	}
	hideBaojiaModal() {
		this.modalBaoJiaVisible = false;
	}





	getCartlist() {
		this.cartlist = [
			{
				id: '1',
				title: '988',
				price: '100',
				amount: '12',
				mark: '12'
			},
			{
				id: '2',
				title: '988',
				price: '100',
				amount: '12',
				mark: '12'
			},
			{
				id: '3',
				title: '988',
				price: '100',
				amount: '12',
				mark: '12'
			},
		];
	}

	gotoMenu(name) {
		console.log(name);
		if (name == 'meeting') {
			this.navCtrl.push(MeetingPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'food') {
			this.navCtrl.push(FoodPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'room') {
			this.navCtrl.push(RoomPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'wedding') {
			// this.navCtrl.push(WeddingPage).then(() => {
			// 	this.navCtrl.removeView(this.navCtrl.getPrevious());
			// });;
		} else {
			console.log("");
		}
	}

	gotoFrame(name) {
		if (name == '') {
			this.navCtrl.push(WeddingDetailPage, { name: name });
		} else if (name == '') {
			this.navCtrl.push(WeddingDetailPage, { name: name });
		} else if (name == '') {
			this.navCtrl.push(WeddingDetailPage, { name: name });
		} else if (name == '') {
			this.navCtrl.push(WeddingDetailPage, { name: name });
		}
	}
	gotoDetail(id) {
		this.navCtrl.push(WeddingDetailPage, { name: '', id: id });
	}


	checkSecond(num, item) {
		this.second = item.id;
		this.secondname = item.name;
		this.showedit = false;
		if(item.id == 2){this.getSetList()}
		if(item.id == 3){this.getBrightLight(0)}
		if(item.id == 4){this.getServicePerson(3)}
	}

	chooseType(num) {
		this.chooseTypeValue = num;
		// this.getChooseList();
	}

	addPerson() {
		this.modalPersonVisible = true;
	}
	hidePersonModal() {
		this.modalPersonVisible = false;

	}

	formAddPersonSubmit(form) {
		form.img = this.imgUpLoad;
		console.log(form);
		this.service.insertSp(this.token, this.account_id, form.name, form.img, form.telephone, this.selectValue).then(data => {
			this.modalPersonVisible = false;
		})
	}

	// base64
	public uploadfn2(data) {
		this.imgUpLoad = data;
	}
}



